
.sidebar {
    width: 270px;
    height: 100vh;
    background: $bg-black-100;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    z-index: 10;
    transition: all .4s;
    &-title {
        font-size: 42px;
        position: absolute;
        letter-spacing: 4px;
        top: 50px;
        left: 50%;
        transform: translate(-50%,0);
        & a {
            text-decoration: none;
            font-weight: 600;
            position: relative;
            color: $text-black-900;
            &::before {
                @include frame;
                border-bottom: 5px solid var(--skin-color);
                border-left: 5px solid var(--skin-color);
                bottom: 0;
                left: 0;
            }
            &::after {
                @include frame;
                border-top: 5px solid var(--skin-color);
                border-right: 5px solid var(--skin-color);
                top: 0;
                right: 0;
            }
            & span {
                font-family: 'Clicker Script', cursive;
                font-size: 40px;
            }
        }
    }
    &-button {
        position: fixed;
        top: 20px;
        left: 300px;
        background: $bg-black-100;
        cursor: pointer;
        width: 45px;
        height: 40px;
        display: none;
        justify-content: center;
        align-items: center;
        border: 1px solid $bg-black-50;
        transition: all .4s;
        & span {
            @include sidebarButton;
            position: relative;
        }
        &::before {
            content: "";
            @include sidebarButton;
            top: 10px;
        }
        &::after {
            content: "";
            @include sidebarButton;
            bottom: 10px;
            
        }
    }
    &-nav {
        margin-top: 50px;
        li{
            display: block;
            margin-bottom: 35px;
        }
        &__item {
            text-decoration: none;
            font-size: 22px;
            font-weight: 600;
            display: block;
            border-bottom: 2px solid $bg-black-50;
            color: $text-black-900;
            padding: 5px 15px;
            &.active {
                color: var(--skin-color);
            }
            & i {
                width: 25px;
                height: 25px;
                margin-right: 15px;
            }

        }
        &__img {
            width: 35px;
            height: 35px;
            margin: 0 10px 0 0;
        }
    }
}

