///bg-colors///
$bg-black-900: #f2f2fc;
$bg-black-100: #fdf9ff;
$bg-black-50: #e8dfec;

///text-colors///
$text-black-900: #302e4d;
$text-black-700: #504e70;

@mixin frame {
    content: "";
    position: absolute;
    height: 25px;
    width: 25px;
    margin: -15px -20px;
}

@mixin sidebarButton {
    position: absolute;
    width: 18px;
    height: 2px;
    background: var(--skin-color);
    display: inline-block;
}

@mixin pad-15 {
    padding-left: 15px;
    padding-right: 15px;
}

@mixin titleDecor {
    content: "";
    position: absolute;
    height: 3px;
    background: var(--skin-color);
    left: 0;
}

@mixin aboutPoint {
    content: "";
    background: var(--skin-color);
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(20px, 0);
}