.main {
    min-width: 100vh;
    display: flex;
    color: $text-black-900;
    &-info {
        flex: 0 0 60%;
        max-width: 60%;
        @include pad-15;
        &__title {
            font-size: 26px;
            margin: 15px 0 30px;
            & span {
                font-size: 30px;
                font-weight: 600;
                color: var(--skin-color);
            }
        }
        &__intro {
            font-size: 26px;
            margin: 0 0 30px;
            & .type-animation {
                color: var(--skin-color);
                font-weight: 600;
            }
        }
        &__text {
            font-size: 22px;
            color: $text-black-900;
            margin: 0 0 70px;
            line-height: 150%;
        }
    }
    &-img {
        flex: 0 0 40%;
        max-width: 40%;
        text-align: center;
        @include pad-15;
        position: relative;
        img{
            height: 400px;
            border-radius: 5px;
        }
        &::before {
            content: "";
            position: absolute;
            height: 80px;
            width: 80px;
            border-top: 8px solid var(--skin-color);
            border-left: 8px solid var(--skin-color);
            top: 0;
            left: 0;
            transform: translate(50%, -50%)
        }
        &::after{
            content: "";
            position: absolute;
            height: 80px;
            width: 80px;
            border-bottom: 8px solid var(--skin-color);
            border-right: 8px solid var(--skin-color);
            bottom: 0;
            right: 0;
            transform: translate(-50%, 50%)
        }
}   }

