.about {
    min-width: 100vh;
    color: $text-black-900;
    & .row {
        justify-content: space-between;
    }
    &-text {
        margin: 45px 0 30px;
        font-size: 24px;
        color: $text-black-900;
        line-height: 150%;
    }
    &-info {
        @include pad-15;
        margin: 0 0 70px;
        width: 33.33%;
        &__title{
            margin: 30px 0;
            font-size: 24px;
            border-bottom: 2px solid var(--skin-color);
            & span {
                font-weight: 600; 
            }
        }
    }
    &-box {
        width: calc(50% - 30px);
        background-color: $bg-black-100;
        box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
        margin: 0 15px;
        border-radius: 4px;
        &__title{
            text-align: center;
            margin: 20px;
            font-size: 24px;
            font-weight: 600;

        }
        &__content {
            padding: 0 60px 35px;
            font-size: 20px;
            position: relative;
            &::before {
                @include aboutPoint;
                width: 2px;
                height: calc(100% - 35px);
            }
        }
        &__info {
            margin-bottom: 35px;
            position: relative;
            &:last-child {
                margin-bottom: 0;
            }
            &::before {
                @include aboutPoint;
                height: 22px;
                width: 22px;
                transform: translate(-50px, 0);
            }
        }
        &__date {
            font-weight: 700;
            margin-bottom: 10px;
            & i {
                margin-right: 10px;
            }
        }
        &__subtitle {
            margin-bottom: 10px;
            font-size: 22px;
            font-weight: 500;
        }


    }
}