.home {
    padding-left: 270px;
}
.section {
    background: $bg-black-900;
    min-height: 100vh;
    display: block;
    padding: 0 30px;
    &.hidden {
        display: none;
    }
    & .container {
        padding: 50px 0 60px;
    }
    &-title {
        flex: 0 0 100%;
        max-width: 100%;
        @include pad-15;
        & h2 {
            font-size: 40px;
            font-weight: 600;
            color: $text-black-900;
            position: relative;
            &::after {
                @include titleDecor;
                width: 5%;
                bottom: -15px;
            }
            &::before{
                @include titleDecor;
                width: 10%;
                bottom: -5px;
            }
        }
    }
}
.container {
    max-width: 1140px;
    width: 100%;
    margin: auto;
} 
.row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;
    position: relative;
}

.btn {
    font-size: 18px;
    padding: 15px 30px;
    background: var(--skin-color);
    text-decoration: none;
    border-radius: 16px;
    color: rgb(238, 233, 233);
}

